.container-profile{
    width:100%;
    overflow-y: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #fff;
    min-height: calc(100vh - 4rem);
}

.container-profile img{
    padding: 3rem;
    width: 20rem;
    height:20rem;
    border-radius: 50%;
}