@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
.container-login{
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #fff;
    min-height: 100vh;
}

.img-logo{
    width: 20em;
}

.container-login h5{
    width: 27em;
}

#react-app{
    padding-bottom: 1rem;
}

.container-login a{
    font-size: 3em;
}

button{
    width: 10em;
}

button span{
    color:#000;
}

@media only screen
and (min-device-width: 375px)
and (max-device-width: 812px) 
and (-webkit-min-device-pixel-ratio: 3) { 

    .container-login{
        width: 100%;
        margin-top: 2rem;
        overflow: hidden;
    }
    
    .container-login h5{
        width: 80%;
        font-size: 1rem;
    }
}
.container{
    width:100%;
    padding:0!important;
    margin:0!important;
    max-width: 100%!important;
    min-height: calc(100vh - 4rem);
    background: #fff;
}

.fa{
    cursor: pointer;
}

.top-container{
    width: 70%;
    margin: 0 auto;
}

.container img{
    display: block;
    margin: 0 auto;
}

.wrapper-top-header h1{
    padding-bottom: 2rem;
    font-size: 3rem;
}

.container audio{
    width: 100%;
    display:block;
    margin: 0 auto;
}

.container-current-top{
    padding-top: 2rem;
    padding-bottom: 5rem;
    display:flex;
    justify-content: center;
}

.wrapper-top-preview{
    width: 30rem;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.container-favorite-genres{
    padding-bottom: 4rem;
    width:100%;
}

.progress{
    width: 25%;
    margin-top: 0.5rem;
}

.genres{
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.genre-name{
    width:20%;
    text-align:center;
}


.fontawesome{
    width: 100px;
    height:100px;
    display:block;
}

.img-artwork{
    box-shadow: 0 0 20px #000;
    width: 90%;
}

@media only screen
and (min-device-width: 375px)
and (max-device-width: 828px) {

    .container{
        overflow-x: hidden;
        position: relative;
    }

    .container-current-top{
        flex-direction: column;
    }

    .wrapper-top-header h1{
        font-size: 2rem;
    }

    .wrapper-top-header img{
        width: 60%;
        border-radius: 10px;
        margin-bottom: 2rem;
    }

    .wrapper-top-preview{
        width: 100%;
    }

    .wrapper-top-preview h4{
        font-size: 1.2rem;
    }

    .container-favorite-genres h1{
        font-size: 2rem;
    }

    .container-favorite-genres h4{
        font-size: 1.2rem;
    }

    .genres h4{
        font-size: 1rem;
    }

    .progress{
        display:none;
    }

    #tp-artist-2{
        order: 2;
    }

    #tp-artist-1{
        order: 1;
    }

    .genre-name{
        width: 50%;
    }
}
.container-profile{
    width:100%;
    overflow-y: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #fff;
    min-height: calc(100vh - 4rem);
}

.container-profile img{
    padding: 3rem;
    width: 20rem;
    height:20rem;
    border-radius: 50%;
}
h6{
    margin:0;
    padding:0;
}

.search-container{
    background: #fff;
    min-height: calc(100vh - 4rem);
}

input:focus::-webkit-input-placeholder{
    color: transparent;
}

input:focus::-moz-placeholder{
    color:transparent;
}

input:focus::-ms-input-placeholder{
    color:transparent;
}

.search-result{
    padding-top: 3rem;
}

.album-header{
    margin-left: 4rem;
    margin-right: 4rem;
    border-top: 1px black dotted;
    border-bottom: 1px black dotted;
}

.album-header:hover{
    cursor: pointer;
}

.text-center .form-control{
    width: 20rem!important;
    margin: 1rem auto;
    text-align:center;
    background: #fff!important;
    border: 2px #000 solid;
    color: #000;
}

.text-center .form-control:focus, 
.text-center  .form-control:hover{
    background: #bbb!important;
}

#album-container{
    position: relative;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    padding: 0 2rem;
}

.btn{
    width: 20rem!important;
    color: black!important;
}

.search-result h6{
    width: 20rem;
}

.form-group{
    position: relative;
    width: 10rem;
    margin: 0 auto;
}

.form-group p{
    text-align: center;
    margin-bottom: 0;
}

.form-group .form-control{
    width: 100%;
}

.image-artwork{
    box-shadow: 0 0 20px #000;
    border-radius: 10px;
}

.image-artwork:hover{
    opacity: 0.7;
    cursor: pointer;
}

@media only screen
and (min-device-width: 375px)
and (max-device-width: 828px) {
    .modal-main{
        flex-direction: column;
        overflow-x: hidden;
        position: relative;
        border-radius: 7px!important;
    }

    .artwork-container{
        display:none;
    }
}
hr{
    margin: 0 3em 0 3em;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
}

.modal-main {
    position:fixed;
    display: flex;
    background: #fff;
    overflow:auto;
    width: 80%;
    height: 90vh;
    min-height: auto;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    border-radius: 20px;
}

.modal-main::-webkit-scrollbar{
    display:none;
}

.track-list-container{
    padding: 3em 3em 3em 5em;
}

.display-block {
    display: block!important;
}

.display-none {
    display: none;
}

.artwork-container{
    flex-direction: column;
}

.track-info h2,
.track-info p{
    width: 12em;
    margin-left: 3rem;
    margin-bottom: 0;
}
.track-info audio{
    margin-left: 3rem;
}

.track-item{
    font-size: 1.5rem;
    color: #000;
    cursor: pointer;
}

.track-item:hover{
    position: relative;
}

.track-item i{
    opacity: 0;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
    margin: 0.2em;
}

.track-item:hover i{
    opacity: 1;
}

.track-artwork{
    width: 80%;
    align-self: flex-start;
    margin: 3rem;
    box-shadow: 0 0 20px #000;
    border-radius: 10px;
}

.modal-close-button{
    position: absolute;
    right: 1em;
    top: 1em;
    font-size: 25px;
    color: red;
}

.modal-close-button:hover{
    background: #bbb;
    font-weight: bold;
}

@media only screen
and (min-device-width: 375px)
and (max-device-width: 828px) {
    .track-list-container{
        padding: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .track-list-container h1{
        margin-top: 2rem;
        margin-left: 2rem;
        margin: 2rem 0 3rem 2rem;
        font-size: 1.5rem;
    }

    .track-item{
        font-size: 0.8rem;
    }

    .track-item i{
        display: none;
    }
}
body{
    overflow-x: hidden;
}

.top-track-container{
    background: #fff;
    min-height: calc(100vh - 4rem);
}

.top-tracks-wrapper{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.track-list:hover{
    cursor: pointer;
    color: darkblue;
}

.track-list i{
    opacity: 0;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
}

.track-list:hover i{
    display: inline-block;
    opacity: 0.8;
}

.image-container{
    display: flex;
}

.track-img{
    width: 100%;
    max-width: 30em;
    box-shadow: 0px 10px 15px rgba(0, 0, 0, 1);
    margin-top: 2em;
}

.image-container div p{
    text-align: center;
    font-weight: bold;
    font-size: 16px;
}

.image-container div i{
    font-size: 30px;
}

.image-container div{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 8rem;
    width: 100%;
}

.image-container div audio{
    width: 80%;
}

.image-container div audio:focus{
    outline: none;
}

@media only screen
and (min-device-width: 375px)
and (max-device-width: 828px) {

    .top-track-container{
        overflow-x: hidden;
        position: relative;
    }

    .col-lg-4 p{
        margin-top: 1rem;
        text-align: center;
        font-size: 1.2rem;
    }

    .col-lg-4{
        order: 2;
    }

    .col-lg-8{
        order: 1;
    }

    .track-container{
        margin-left: 1rem;
        padding-right: 20px;
    }

    .track-img{
        width: 60%;
        margin: 2em auto 0 auto;
    }

    .image-container{
        flex-direction: column;
    }

    .image-container div{
        padding-top: 2rem;
    }
}
.top-artists-container{
    background: #fff;
    min-height: calc(100vh - 4rem);
}

@media only screen
and (min-device-width: 375px)
and (max-device-width: 828px) {

    .artist-list{
        margin-left: 2rem;
    }

}
.nav-item a{
    color: #fff;
}

.nav-link{
    color:#fff;
}

.navbar-toggler-icon{
    opacity: 0.5;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

